<template>
  <v-container fluid grid-list-md>
    <v-toolbar dense app>
      <img src="static/img/logo.svg" height="80%" class="pa-1" alt="Time Cop">
      <v-spacer></v-spacer>
      <v-btn flat color="blue" @click="toggleDataVisibility = !toggleDataVisibility">
        data
        <v-icon v-if="toggleDataVisibility" right>visibility</v-icon>
        <v-icon v-else right>visibility_off</v-icon>
      </v-btn>
    </v-toolbar>
    <v-layout wrap>
      <v-flex :class="toggleDataVisibility ? 'xs8' : 'xs12'">
        <t-graph-2d :dataSet="response" :toggleSize="toggleDataVisibility" :height="450" :marginLeft="70"></t-graph-2d>
      </v-flex>
      <v-flex xs4 v-show="toggleDataVisibility" column-code>
        <t-form @response="showResponse"></t-form>
        <t-json :json="response.prediction"></t-json>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import tForm from '@/components/form'
import tJson from '@/components/jsonViewer'
import tGraph2d from '@/components/graph2D'
export default {
  name: 'homeView',
  components: {
    tForm,
    tJson,
    tGraph2d
  },
  data: () => ({
    response: {},
    toggleDataVisibility: true
  }),
  methods: {
    toggleData () {
      this.toggleDataVisibility = true
    },
    showResponse (e) {
      this.response = {
        toPredict: e.dataToProcess,
        prediction: e.result
      }
    }
  }
}
</script>

<style>
.column-code {
  height: 85vh;
  overflow: scroll;
}
</style>



