import yAxis from './components/y-axis'
import xAxis from './components/x-axis'
import cBars from './components/cBars'
import cPath from './components/cPath'
const chartFunctions = {
  install (Vue) {
    Vue.prototype.$getMax = (data, key) => {
      let max = 0
      if (key) {
        for (let i = 0; i < data.length; i++) {
          max = data[i][key] > max ? data[i][key] : max
        }
      } else {
        for (let i = 0; i < data.length; i++) {
          max = data[i] > max ? data[i] : max
        }
      }
      return max
    }
    Vue.prototype.$getMin = (data, key) => {
      let min = 10e10
      if (key) {
        for (let i = 0; i < data.length; i++) {
          min = data[i][key] < min ? data[i][key] : min
        }
      } else {
        for (let i = 0; i < data.length; i++) {
          min = data[i] < min ? data[i] : min
        }
      }
      return min
    }
    Vue.prototype.$toYPixel = (point, min, max, lenght) => {
      return lenght - (point - min) / (max - min) * lenght
    }
    Vue.prototype.$toXPixel = (point, min, max, lenght) => {
      return (point - min) / (max - min) * lenght
    }
    Vue.prototype.$getRandomColor = () => `hsla(${Math.floor(Math.random() * 360)}, 100%, 70%, 60)`
    Vue.component(yAxis.name, yAxis)
    Vue.component(xAxis.name, xAxis)
    Vue.component(cBars.name, cBars)
    Vue.component(cPath.name, cPath)
  }
}

export default chartFunctions



