<template>
  <v-flex xs12 v-resize="calculateSize">
    <v-toolbar dense dark>
      <v-toolbar-items>
        <v-btn color="blue" @click="isActive.toPredict = !isActive.toPredict" flat v-if="toPredict.length > 0">Main</v-btn>
        <v-btn color="yellow" @click="isActive.prediction = !isActive.prediction" flat v-if="prediction.length > 0">Prediction</v-btn>
        <v-btn 
          v-for="(timeSerie, i) in timeSeries"
          :key="'cheboxTimeSerie' + i"
          :color="timeSeries[i].color"
          v-if="timeSeries.length > 0"
          @click="isActive[i] = !isActive[i]" flat>data-{{i}}</v-btn>
        <v-btn
          v-for="(d, i) in debug"
          :key="'debug' + i"
          v-if="debug.length > 0"
          :color="d.color"
          @click="isActive.debug[d.name] = !isActive.debug[d.name]" flat>
          {{d.name}}
          <span v-if="d.name === dataSet.prediction.engine" class="winner">&#9813;</span>
        </v-btn>
      </v-toolbar-items>
      <v-spacer></v-spacer>
      <v-tooltip bottom>
        <v-btn slot="activator" flat @click="randomizeColors" icon>
          <v-icon>brush</v-icon>
        </v-btn>
        <span>Randomize Colors</span>
      </v-tooltip>
    </v-toolbar>
    <svg width="100%" :height="height" class="grey darken-3"
    @wheel.prevent="zoom"
    @mousedown.exact="panEnabled = true"
    @mouseup="panEnabled = false"
    @mousemove="pan">
      <g :transform="`translate(${marginLeft}, ${marginTop})`" v-if="toPredict.length > 0 && prediction.length > 0">
        <defs>
          <clipPath id="clip-rect">
            <rect :width="chartWidth" :height="height" :y="-this.marginTop" />
          </clipPath>
        </defs>
        <g clip-path="url(#clip-rect)">
        <!-- linea a predecir -->
        <c-path
          v-if="isActive.toPredict"
          :transform="`translate(${-offsetX} 0)`"
          :rangeX="[zoomMin, zoomMax]"
          :rangeY="[globalMin, globalMax]"
          :dataset="toPredict"
          column="y"
          :height="chartHeight"
          :width="chartWidth"></c-path>
        <!-- linea de prediccion (yellow) -->
        <c-path
          v-if="isActive.prediction"
          :transform="`translate(${$toXPixel(prediction[0].x + zoomMin, zoomMin, zoomMax, chartWidth) - offsetX}, 0)`"
          color="yellow"
          :rangeX="[zoomMin, zoomMax]"
          :rangeY="[globalMin, globalMax]"
          :dataset="prediction"
          column="y"
          :height="chartHeight"
          :width="chartWidth"></c-path>
        <!-- text de prediccion -->
        <!-- <text
          v-if="total.length > 0"
          fill="yellow"
          :transform="`translate(${$toXPixel(prediction[prediction.length - 1].x, zoomMin, zoomMax, chartWidth) - offsetX}, ${$toYPixel(prediction[prediction.length - 1].y, globalMin, globalMax, chartHeight)})`">
            Future
        </text> -->

        <!-- timeSeries -->
        <c-path
          v-for="(timeSerie, i) in timeSeries"
          :key="'serie' + i"
          v-if="isActive[i]"
          :transform="`translate(${-offsetX} 0)`"
          :color="timeSerie.color"
          :rangeX="[zoomMin, zoomMax]"
          :rangeY="[globalMin, globalMax]"
          :dataset="timeSerie.dataset"
          column="y"
          :height="chartHeight"
          :width="chartWidth"></c-path>

        <!-- lineas de debug -->
        <g v-for="(data, i) in debug"
          :key="'debug' + i"
          v-if="!debug.isEmpty && isActive.debug[data.name]">
          <c-path
            dasharray="5,5"
            :transform="`translate(${$toXPixel(data.d[0].x + zoomMin, zoomMin, zoomMax, chartWidth) - offsetX}, 0)`"
            :color="data.color"
            :rangeX="[zoomMin, zoomMax]"
            :rangeY="[globalMin, globalMax]"
            :dataset="data.d"
            column="y"
            :height="chartHeight"
            :width="chartWidth"></c-path>
            <!-- <text :fill="data.color"
            :transform="`translate(${$toXPixel(data.d[0].x + data.d.length, zoomMin, zoomMax, chartWidth) - offsetX}, ${$toYPixel(data.d[data.d.length - 1].y, globalMin, globalMax, chartHeight)})`">
              {{data.name}}
            </text> -->
        </g>

        <!-- marcas de anomalias -->
        <line
        v-if="markPos.pos > 0"
        :x1="markPos.pos"
        :x2="markPos.pos"
        :y2="chartHeight"
        stroke-width="2"
        stroke="#0eff0e78"
        fill="none"/>
        <text fill="#0eff0e78"
        v-if="markPos.pos > 0"
        text-anchor="middle"
        dy="-5px"
        :transform="`translate(${markPos.pos} 0)`">
          {{markPos.val}}
        </text>
        <circle
        v-for="(line, i) in anomalies"
        :key="i"
        :cx="line - offsetX" :cy="chartHeight" r="7" stroke="white" stroke-width="1" fill="red" />
        <!-- ejes -->
        <x-axis
        :transform="`translate(${-offsetX} ${chartHeight})`"
        :range="[zoomMin, zoomMax]"
        :dataset="total"
        x="x"
        :ticks="25"
        :fixed="1"
        :height="chartHeight"
        :width="chartWidth"
        label="Time"></x-axis>
        </g>
        <y-axis :range="[globalMin, globalMax]" :ticks="5" :fixed="1" :height="chartHeight" label="Units"></y-axis>
      </g>
    </svg>
  </v-flex>
</template>

<script>
export default {
  props: {
    dataSet: null,
    height: {
      type: Number,
      default: 500
    },
    marginLeft: {
      type: Number,
      default: 40
    },
    marginTop: {
      type: Number,
      default: 40
    },
    marginRight: {
      type: Number,
      default: 20
    },
    marginBottom: {
      type: Number,
      default: 30
    },
    padding: {
      type: Number,
      default: 12
    },
    toggleSize: {
      type: Boolean
    }
  },
  data: () => ({
    width: null,
    chartWidth: 1000,
    chartHeight: 400,
    total: [],
    toPredict: [],
    prediction: [],
    debug: [],
    timeSeries: [],
    zoomMin: 0,
    zoomMax: 100,
    panEnabled: false,
    offsetX: 0,
    globalMax: -10e10,
    globalMin: 10e10,
    isActive: {
      toPredict: true,
      prediction: true,
      debug: {}
    },
    markPos: {
      pos: 0,
      val: 0
    }
  }),
  mounted () {
    this.calculateSize()
  },
  methods: {
    calculateSize () {
      this.width = this.$el.clientWidth
      this.chartWidth = this.width - (this.marginLeft + this.marginRight)
      this.chartHeight = this.height - (this.marginTop + this.marginBottom)
    },
    drawData (d) {
      // check if is multi or uni
      // to predict
      let toPredict = d.toPredict.main || d.toPredict.data
      for (let i = 0; i < toPredict.length; i++) {
        const val = toPredict[i]
        this.toPredict.push({
          x: i,
          y: val
        })
        this.total.push({
          x: i,
          y: val
        })
      }
      // prediction
      // enlazar toPredict y prediction
      let prediction = d.prediction.future
      this.toPredict.push({
        x: toPredict.length,
        y: prediction[0].value || prediction[0]['expected value']
      })
      for (let i = 0; i < prediction.length; i++) {
        const val = prediction[i].value || prediction[i]['expected value']
        this.prediction.push({
          x: prediction[i].step,
          y: val
        })
        this.total.push({
          x: prediction[i].step,
          y: val
        })
      }
      // check for timeseries
      const timeseries = d.toPredict.timeseries
      if (timeseries && timeseries.length) {
        for (let t = 0; t < timeseries.length; t++) {
          this.$set(this.timeSeries, t, {
            // color: '#b17520',
            color: this.$getRandomColor(),
            dataset: []
          })
          for (let i = 0; i < timeseries[t].data.length; i++) {
            this.timeSeries[t].dataset.push({
              x: i,
              y: timeseries[t].data[i]
            })
          }
          this.$set(this.isActive, t, true)
        }
      }
      this.zoomMax = this.total.length - 1
      this.globalMax = this.$getMax(this.total, 'y')
      this.globalMin = this.$getMin(this.total, 'y')
      // debug
      if (d.prediction.debug) {
        for (const key in d.prediction.debug) {
          const engine = d.prediction.debug[key]
          this.$set(this.isActive.debug, key, true)
          this.debug.push(
            {
              name: key,
              d: [],
              color: this.$getRandomColor()
            }
          )
          engine.map(v => {
            this.debug[this.debug.length - 1].d.push({
              x: v.step,
              y: v['expected value'] || v.Prediction
            })
            this.globalMax = this.globalMax > v['expected value'] || v.Prediction ? this.globalMax : v['expected value'] || v.Prediction
            this.globalMin = this.globalMin < v['expected value'] || v.Prediction ? this.globalMin : v['expected value'] || v.Prediction
          })
        }
      }
    },
    zoom (e) {
      const delta = e.wheelDelta ? e.wheelDelta * 0.02 : -e.deltaY
      if (this.zoomMin && this.zoomMax >= 3000) {
        this.zoomMin = 0
        this.zoomMax = this.total.length - 1
      } else {
        this.zoomMin += delta
        this.zoomMax += -delta
      }
    },
    pan (e) {
      if (this.panEnabled) {
        this.offsetX += -e.movementX
        this.markPos = 0
      } else {
        const position = e.offsetX - this.marginLeft
        for (let i = 0; i < this.total.length; i++) {
          let p = this.$toXPixel(i, this.zoomMin, this.zoomMax, this.chartWidth) - this.offsetX
          if (p < position) {
            this.markPos = {
              pos: p,
              val: i
            }
          }
        }
      }
    },
    randomizeColors () {
      for (let i = 0; i < this.debug.length; i++) {
        this.debug[i].color = this.$getRandomColor()
      }
      for (let i = 0; i < this.timeSeries.length; i++) {
        this.timeSeries[i].color = this.$getRandomColor()
      }
      // for (const key in this.colors) {
      //   this.colors[key] = this.$getRandomColor()
      // }
    },
    reset () {
      this.total = []
      this.toPredict = []
      this.prediction = []
      this.globalMax = -10e10
      this.globalMin = 10e10
      this.debug = []
      this.timeSeries = []
      this.zoomMin = 0
      this.zoomMax = 100
      this.offsetX = 0
      this.isActive = {
        toPredict: true,
        prediction: true,
        debug: {}
      }
    }
  },
  watch: {
    dataSet: {
      handler: function (val) {
        if (val.toPredict && val.prediction) {
          if (!val.prediction.debug) {
            this.debug.isEmpty = true
          }
          this.$nextTick(() => {
            this.reset()
            this.drawData(val)
          })
        }
      },
      immediate: true
    },
    toggleSize: function () {
      this.calculateSize()
    }
  },
  computed: {
    anomalies () {
      const d = this.dataSet
      if (d.toPredict) {
        let anomalies = []
        for (let i = 0; i < d.prediction.past.length; i++) {
          let xMark = this.$toXPixel(d.prediction.past[i].step, this.zoomMin, this.zoomMax, this.chartWidth)
          anomalies.push(xMark)
        }
        return anomalies
      }
    }
    // colors () {
    //   return {
    //     1: { hexS: '#804040' },
    //     2: { hexS: '#ff0080' },
    //     3: { hexS: '#ff8040' },
    //     4: { hexS: '#800040' },
    //     5: { hexS: '#8080ff' },
    //     6: { hexS: '#008080' },
    //     7: { hexS: '#4CAF50' },
    //     Autoarima: { hexS: '#0080c0' },
    //     Holtwinters: { hexS: '#73b9ff' },
    //     LSTM: { hexS: '#b3ffff' }
    //   }
    // }
  }
}
</script>

<style>
.winner {
  position: absolute;
  right: 0;
  margin-right: -14px;
  margin-top: -10px;
  font-size: 25px;
  transform: rotateZ(28deg);
}
</style>



